@import "../../theme/index.scss";

.header {
  width: 100%;
  height: 80px;
  display: flex;
  z-index: 10;
  box-sizing: border-box;
  background-color: transparent;
  @include respond-to(desktop) {
    height: 90px;
    padding: 20px 130px;
    justify-content: center;
    align-items: flex-start;
  }
}